.group {
    margin: 1em auto;
    max-width: 700px;
    padding: 1em;
}

input {
    border: none;
    border-bottom: 2px solid rgba(245,98,98,.45);
    box-shadow: 0 6px 12px rgba(0,0,0,.23), 0 10px 40px rgba(0,0,0,.19);
    border-radius: 2px;
    font-family: inherit;
    font-size: 20px;
    letter-spacing: 1px;
    padding: 12px;
    transition: border .2s;
    width: 100%;
}

input:active, input:focus {
    border-bottom: 2px solid #f56262;
}

input:hover {
    outline: none;
}

.videoWrapper {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 25px;
    height: 0;
    overflow: hidden;
}

.videoWrapper > div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
}
.VideoView {
    width: 100%;
    max-width: 1200px;
    display: flex;
    margin: 0 auto;
    /*border: 2px solid #253B6E;*/
}

.VideoOne {
    width: 70%;
}

.VideoList {
    width: 30%;
    min-height: 320px;
    max-height: 497px;
    overflow: auto;
    background-color: #fff;
}

.VideoListItem {
    padding: 15px 30px 15px 15px;
}

.VideoListItem:nth-child(odd) {
    background-color: #1F5F8B;
    color: #fff;
}

body {
  background-color: #414141;
  color: rgba(0,0,0,.59);
  font-family: Roboto Mono,monospace;
  line-height: 1.25;
  margin: 0;
}

a {
  color: rgba(0, 0, 0, 0.25);
  text-decoration: none;
  outline: none;
}

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

*:focus {
  outline: none;
}

*:before, *:after {
  box-sizing: border-box;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

h1, h1, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

